html {
  box-sizing: border-box;
  font-family: "DM Sans", "Josefin Sans", sans-serif;
  font-weight: 400;
  background-color: rgb(242, 242, 242);
}

.themeBlack {
  background-color: rgb(65, 65, 65);
}

*,
*::after,
*::before {
  margin: 0;
  box-sizing: inherit;
}

ul,
ol {
  padding: 0;
  list-style-type: none;
}

a {
  text-decoration: none;
  color: inherit;
}

.container {
  max-width: 77.5rem;
  margin: 0 auto;
}

button {
  padding: 0;
}

.header__navbar {
  margin-top: 0.925rem;
}

.navbar {
  padding-inline: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 2.5rem;
}

.menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 2.5rem;
  text-align: center;
}
.menu__item {
  font-size: 1.25rem;
}

.btn {
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.btn__img {
  color: aqua;
  display: block;
}

.burger {
  position: relative;
  width: 30px;
  height: 30px;
  display: none;
  z-index: 5;
}

@media (max-width: 42.5rem) {
  .menu {
    display: none;
  }
  .burger {
    display: block;
  }
  .burger span {
    left: 0;
    position: absolute;
    height: 2px;
    width: 30px;
    background-color: #020202;
  }
  .burger::before {
    top: -8px;
    position: absolute;
    height: 2px;
    width: 30px;
    background-color: #020202;
  }
  .burger::after {
    top: 8px;
    position: absolute;
    height: 2px;
    width: 30px;
    background-color: #020202;
  }
  .burger::before, .burger::after {
    content: "";
  }
  .burger::before {
    top: 0;
  }
  .burger::after {
    bottom: 0;
  }
  .burger span {
    top: 50%;
    transform: translate(0px, -50%);
  }
  .burger:hover::before {
    top: 50%;
    transform: translate(0px, -50%);
    transform: rotate(45deg);
  }
  .burger:hover span {
    transform: translate(0px, -50px);
  }
  .burger:hover::after {
    bottom: 50%;
    transform: translate(0px, 50%);
    transform: rotate(-45deg);
  }
}